              <li><strong>ä¸å¯è§ç©ºæ ¼:</strong> ç‰¹æ®Šç©ºæ ¼å­—ç¬¦</li>
              <li><strong>Unicodeæ§åˆ¶ç¬¦:</strong> æ§åˆ¶å­—ç¬¦</li>
              <li><strong>æ··åˆæ¨¡å¼:</strong> éšæœºç»„åˆå¤šç§ç¬¦å·</li>
            </ul>
          </div>
        </el-col>
        
        <el-col :span="8">
          <div class="info-section">
            <h4>ğŸ”¥ çƒ­é—¨æ¸¸æˆ</h4>
            <ul>
              <li>ç‹è€…è£è€€ - æœ€å—æ¬¢è¿MOBAæ‰‹æ¸¸</li>
              <li>å’Œå¹³ç²¾è‹± - æˆ˜æœ¯ç«æŠ€æ‰‹æ¸¸</li>
              <li>è‹±é›„è”ç›Ÿ - ç»å…¸MOBAæ¸¸æˆ</li>
              <li>åŸç¥ - å¼€æ”¾ä¸–ç•Œå†’é™©æ¸¸æˆ</li>
            </ul>
          </div>
        </el-col>
      </el-row>
    </el-card>

    <!-- IDè¯¦æƒ…å¯¹è¯æ¡† -->
    <el-dialog title="IDè¯¦ç»†ä¿¡æ¯" :visible.sync="showDetailsDialog" width="600px">
      <div v-if="selectedId">
        <el-form label-width="120px">
          <el-form-item label="æ˜¾ç¤ºæ•ˆæœ:">
