          </el-form-item>
          <el-form-item label="å®é™…å†…å®¹:">
            <el-input :value="selectedId.actual" readonly type="textarea" :rows="3"></el-input>
          </el-form-item>
          <el-form-item label="å­—ç¬¦é•¿åº¦:">
            <span>{{ selectedId.actual.length }}</span>
          </el-form-item>
          <el-form-item label="å­—èŠ‚é•¿åº¦:">
            <span>{{ getByteLength(selectedId.actual) }}</span>
          </el-form-item>
          <el-form-item label="æ·»åŠ çš„ç¬¦å·:">
            <span>{{ selectedId.symbols }}</span>
          </el-form-item>
          <el-form-item label="Unicodeç¼–ç :">
            <el-input :value="getUnicodeString(selectedId.actual)" readonly type="textarea" :rows="2"></el-input>
