<script>
import { ClipboardUtil } from '@/util/ClipboardUtil'
import DownloadUtil from '@/util/DownloadUtil'

export default {
  name: 'GameIdDeduplicatorView',
  metaInfo: {
    title: 'IDå»é‡å¤å·¥å…· - æ¸¸æˆæ˜µç§°å»é‡å¤ç”Ÿæˆå™¨',
    meta: [
      { name: 'description', content: 'ä¸ºIDæ·»åŠ ä¸å¯è§ç‰¹æ®Šç¬¦å·ï¼Œè§£å†³ç‹è€…è£è€€ã€å’Œå¹³ç²¾è‹±ã€è‹±é›„è”ç›Ÿã€åŸç¥ã€å´©å3ã€æ˜æ—¥æ–¹èˆŸã€é˜´é˜³å¸ˆã€ç¬¬äº”äººæ ¼ç­‰çƒ­é—¨æ¸¸æˆIDé‡å¤é—®é¢˜ã€‚æ”¯æŒé›¶å®½å­—ç¬¦ã€Unicodeç¬¦å·ç­‰å¤šç§å»é‡æ–¹å¼ã€‚' },
      { name: 'keywords', content: 'IDå»é‡å¤,æ¸¸æˆIDå»é‡å¤,ç‹è€…è£è€€ID,å’Œå¹³ç²¾è‹±ID,è‹±é›„è”ç›ŸID,åŸç¥ID,å´©å3ID,æ˜æ—¥æ–¹èˆŸID,é˜´é˜³å¸ˆID,ç¬¬äº”äººæ ¼ID,æ¸¸æˆæ˜µç§°,IDç”Ÿæˆå™¨,é›¶å®½å­—ç¬¦,Unicodeç¬¦å·,ç‰¹æ®Šç¬¦å·' }
    ]
  },
  data() {
    return {
      originalId: '',
      generateCount: 5,
      symbolType: 'zero-width',
      uniqueIds: [],
      showDetailsDialog: false,
      selectedId: null
    }
  },
  methods: {
    generateUniqueIds() {
      if (!this.originalId.trim()) {
        this.uniqueIds = []
        return
      }

      this.uniqueIds = []
      const baseId = this.originalId.trim()

      for (let i = 0; i < this.generateCount; i++) {
        const uniqueId = this.addInvisibleChars(baseId, i)
        this.uniqueIds.push({
          display: baseId, // æ˜¾ç¤ºåŸå§‹ID
          actual: uniqueId, // å®é™…åŒ…å«ç‰¹æ®Šå­—ç¬¦çš„ID
          symbols: this.getAddedSymbols(i)
        })
      }
    },

    addInvisibleChars(id, index) {
      const symbols = this.getInvisibleSymbols()
      let result = id

      switch (this.symbolType) {
        case 'zero-width':
          // åœ¨ä¸åŒä½ç½®æ’å…¥é›¶å®½å­—ç¬¦
          result = this.insertAtPosition(id, symbols.zeroWidth[index % symbols.zeroWidth.length], index)
          break
        case 'invisible-space':
          // ä½¿ç”¨ä¸å¯è§ç©ºæ ¼
          result = this.insertAtPosition(id, symbols.invisibleSpace[index % symbols.invisibleSpace.length], index)
          break
        case 'unicode-control':
          // ä½¿ç”¨Unicodeæ§åˆ¶ç¬¦
          result = this.insertAtPosition(id, symbols.unicodeControl[index % symbols.unicodeControl.length], index)
          break
        case 'mixed': {
          // æ··åˆä½¿ç”¨å¤šç§ç¬¦å·
          const allSymbols = [...symbols.zeroWidth, ...symbols.invisibleSpace, ...symbols.unicodeControl]
          result = this.insertAtPosition(id, allSymbols[index % allSymbols.length], index)
          break
        }
      }

      return result
    },

    insertAtPosition(id, symbol, index) {
      const positions = [
        0, // å¼€å¤´
        Math.floor(id.length / 3), // 1/3å¤„
        Math.floor(id.length / 2), // ä¸­é—´
        Math.floor(id.length * 2 / 3), // 2/3å¤„
        id.length // ç»“å°¾
      ]

      const position = positions[index % positions.length]
      return id.slice(0, position) + symbol + id.slice(position)
    },

    getInvisibleSymbols() {
      return {
        zeroWidth: [
          '\u200B', // é›¶å®½ç©ºæ ¼
          '\u200C', // é›¶å®½éè¿å­—ç¬¦
          '\u200D', // é›¶å®½è¿å­—ç¬¦
          '\u2060', // å•è¯è¿æ¥ç¬¦
          '\uFEFF'  // é›¶å®½éæ–­ç©ºæ ¼
        ],
        invisibleSpace: [
          '\u00A0', // ä¸é—´æ–­ç©ºæ ¼
          '\u2000', // Enå››åˆ†ç©ºæ ¼
          '\u2001', // Emç©ºæ ¼
          '\u2002', // Enç©ºæ ¼
          '\u2003', // Emç©ºæ ¼
          '\u2004', // ä¸‰åˆ†ä¹‹ä¸€Emç©ºæ ¼
          '\u2005', // å››åˆ†ä¹‹ä¸€Emç©ºæ ¼
          '\u2006', // å…­åˆ†ä¹‹ä¸€Emç©ºæ ¼
          '\u2007', // æ•°å­—ç©ºæ ¼
          '\u2008', // æ ‡ç‚¹ç©ºæ ¼
          '\u2009', // ç»†ç©ºæ ¼
          '\u200A'  // å‘ä¸ç©ºæ ¼
        ],
        unicodeControl: [
          '\u202A', // å·¦åˆ°å³åµŒå…¥
          '\u202B', // å³åˆ°å·¦åµŒå…¥
          '\u202C', // å¼¹å‡ºæ–¹å‘æ ¼å¼
          '\u202D', // å·¦åˆ°å³è¦†ç›–
          '\u202E'  // å³åˆ°å·¦è¦†ç›–
        ]
      }
    },

    getAddedSymbols(index) {
      const symbolNames = {
        'zero-width': ['é›¶å®½ç©ºæ ¼', 'é›¶å®½éè¿å­—ç¬¦', 'é›¶å®½è¿å­—ç¬¦', 'å•è¯è¿æ¥ç¬¦', 'é›¶å®½éæ–­ç©ºæ ¼'],
        'invisible-space': ['ä¸é—´æ–­ç©ºæ ¼', 'Enå››åˆ†ç©ºæ ¼', 'Emç©ºæ ¼', 'Enç©ºæ ¼', 'Emç©ºæ ¼', 'ä¸‰åˆ†ä¹‹ä¸€Emç©ºæ ¼', 'å››åˆ†ä¹‹ä¸€Emç©ºæ ¼', 'å…­åˆ†ä¹‹ä¸€Emç©ºæ ¼', 'æ•°å­—ç©ºæ ¼', 'æ ‡ç‚¹ç©ºæ ¼', 'ç»†ç©ºæ ¼', 'å‘ä¸ç©ºæ ¼'],
        'unicode-control': ['å·¦åˆ°å³åµŒå…¥', 'å³åˆ°å·¦åµŒå…¥', 'å¼¹å‡ºæ–¹å‘æ ¼å¼', 'å·¦åˆ°å³è¦†ç›–', 'å³åˆ°å·¦è¦†ç›–'],
        'mixed': ['æ··åˆç¬¦å·']
      }

      const names = symbolNames[this.symbolType] || ['æœªçŸ¥ç¬¦å·']
      return names[index % names.length]
    },

    getByteLength(str) {
      return new Blob([str]).size
    },

    getUnicodeString(str) {
      return str.split('').map(char => {
        const code = char.charCodeAt(0)
        return `U+${code.toString(16).toUpperCase().padStart(4, '0')}`
      }).join(' ')
    },

    showIdDetails(id) {
      this.selectedId = id
      this.showDetailsDialog = true
    },

    async copyId(id) {
      try {
        const success = await ClipboardUtil.copyText(id)
        if (success) {
          this.$message.success('IDå·²å¤åˆ¶åˆ°å‰ªè´´æ¿')
        } else {
          this.$message.error('å¤åˆ¶å¤±è´¥')
        }
      } catch (error) {
        this.$message.error('å¤åˆ¶å¤±è´¥: ' + error.message)
      }
    },

    async copyAllIds() {
      if (this.uniqueIds.length === 0) {
        this.$message.warning('æ²¡æœ‰å¯å¤åˆ¶çš„ID')
        return
      }

      try {
        const allIds = this.uniqueIds.map(id => id.actual).join('\n')
        const success = await ClipboardUtil.copyText(allIds)
        if (success) {
          this.$message.success('æ‰€æœ‰IDå·²å¤åˆ¶åˆ°å‰ªè´´æ¿')
        } else {
          this.$message.error('å¤åˆ¶å¤±è´¥')
        }
      } catch (error) {
        this.$message.error('å¤åˆ¶å¤±è´¥: ' + error.message)
      }
    },

    downloadIds() {
      if (this.uniqueIds.length === 0) {
        this.$message.warning('æ²¡æœ‰å¯ä¸‹è½½çš„ID')
        return
      }

      const content = this.uniqueIds.map((id, index) => {
        return `ID${index + 1}: ${id.actual}\næ˜¾ç¤º: ${id.display}\nç¬¦å·: ${id.symbols}\né•¿åº¦: ${id.actual.length}\nå­—èŠ‚: ${this.getByteLength(id.actual)}\n---`
      }).join('\n')

      const filename = DownloadUtil.generateTimestampFilename('å”¯ä¸€ID', 'txt')
      DownloadUtil.downloadText(content, filename)
      this.$message.success('æ–‡ä»¶ä¸‹è½½æˆåŠŸ')
    },

    loadSample() {
      const samples = ['æ— æ•Œå°æ³•å¸ˆ', 'ç»åœ°æ±‚ç”Ÿç‹', 'å¾·ç›è¥¿äºšä¹‹åŠ›', 'æ—…è¡Œè€…', 'èˆ°é•¿', 'åšå£«', 'é˜´é˜³å¸ˆå¤§äºº', 'æ±‚ç”Ÿè€…', 'ç¤ºä¾‹ID']
      this.originalId = samples[Math.floor(Math.random() * samples.length)]
      this.generateUniqueIds()
    },

    clearAll() {
      this.originalId = ''
      this.uniqueIds = []
    }
  }
}
